import React from 'react';
import { makeStyles, Button } from "@material-ui/core";
import { withRouter } from "react-router-dom";
import { useSwipeable } from "react-swipeable";
import { TOOLBAR_HEIGHT, BROWSE_BY_TITLE_PATH, REQUESTS_PATH, ADMIN } from "../../constants/index";

const useStyles = makeStyles(theme => ({
  admin: {
    minHeight: `calc(100vh - ${TOOLBAR_HEIGHT}px - ${theme.spacing(3)}px)`,
    width: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: "flex-start",
    paddingTop: theme.spacing(3)
  },
  button: {
    margin: theme.spacing(1)
  },
}));

const Admin = props => {
  const { history, setSelectedButton, clearRequests, reloadDatabase } = props;
  const classes = useStyles();

  React.useEffect(() => {
    setSelectedButton(ADMIN);
    return () => {
      setSelectedButton(null);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handlers = useSwipeable({
    onSwipedLeft: () => history.push(REQUESTS_PATH),
    onSwipedRight: () => history.push(BROWSE_BY_TITLE_PATH),
    preventDefaultTouchmoveEvent: true,
    trackMouse: true
  });

  return (<div className={classes.admin} {...handlers}>
    <Button className={classes.button} onClick={clearRequests} variant="outlined">
      Clear Requests
    </Button>
    <Button className={classes.button} onClick={reloadDatabase} variant="outlined">
      Reload Database
    </Button>
  </div>);
}

export default withRouter(Admin);