import { connect } from "react-redux";
import Admin from "./Admin";
import { setSelectedButton, clearRequests, reloadDatabase } from '../../store/actions/index';


const mapDispatchToProps = dispatch => ({
  setSelectedButton: string => dispatch(setSelectedButton(string)),
  clearRequests: () => dispatch(clearRequests()),
  reloadDatabase: () => dispatch(reloadDatabase()),
})
export default connect(null, mapDispatchToProps)(Admin);